$text-color: #252b42;

$p-color: #737373;

$sea-color: #00a0c1;

$orange: #ffa62b;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  letter-spacing: 0.2px;
  color: $text-color;
  background-color: #f3f3f3;
}

a {
  text-decoration: none;
  color: $text-color;
}

.container {
  padding: 0 40px;
  margin: 0 auto;
  max-width: 1440px;
  @media screen and (min-width: 1024px) {
    padding: 0 100px;
  }
}

.title {
  font-weight: 700;
  font-size: 2.5em; //40px
  line-height: 1.425em; //57px
  text-align: center;
  max-width: 460px;
  margin: 0 auto;
  margin-bottom: 10px;
}

.subtitle {
  font-weight: 500;
  font-size: 0.875em; //14px
  line-height: 1.428571428571429em; //20px
  text-align: center;
  color: $p-color;
  max-width: 500px;
  margin: 0 auto;
}

.ico-card {
  font-size: 3em;
  color: $sea-color;
  font-weight: bold;
}
