.contact {
  &-container {
    padding: 75px 0 130px;
  }
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    margin-bottom: 60px;
  }
}

.contact-cards {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  padding: 0 20px;
  &__card {
    min-width: 250px;
    min-height: 330px;
    padding: 80px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.875em; //14px
    line-height: 1.714285714285714em; //24px
    background-color: #fff;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    &__ico {
      font-size: 4em;
      color: $sea-color;
      margin-bottom: 15px;
      font-weight: 700;
    }
    &__bold {
      font-weight: 700;
      margin: 20px 0 10px;
    }
    &__button {
      min-height: 44px;
      min-width: 160px;
      background-color: transparent;
      border: 1px solid $orange;
      border-radius: 5px;
      color: $orange;
      padding: 10px 20px;
      cursor: pointer;
    }
  }
  .contact-cards__mid-card {
    min-height: 413px;
    background-color: #252b42;
    color: #fff;
    &__ico {
      color: #fff;
    }
    &__button {
      border-color: #fff;
      color: #fff;
    }
  }
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    &__card {
      &:not(:last-child) {
        margin-bottom: unset;
      }
    }
  }
}

.consulting {
  background-color: #fff;
  &-container {
    padding: 40px 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (min-width: 768px) {
      flex-direction: unset;
      justify-content: space-between;
    }
  }
  &__title-text {
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__title {
      font-size: 1.5em; //24px
      line-height: 1.333333333333333em; //32px
      font-weight: 700;
      margin-bottom: 10px;
    }
    &__text {
      font-size: 0.875em; //14px
      line-height: 1.428571428571429em; //20px
      margin-bottom: 50px;
      color: $p-color;
      @media screen and (min-width: 768px) {
        margin-bottom: unset;
      }
    }
  }
  &__btn {
    border: 0;
    background-color: $orange;
    min-height: 60px;
    min-width: 160px;
    border-radius: 5px;
    color: #fff;
    padding: 15px 40px;
    align-self: flex-start;
    cursor: pointer;
  }
}
