.clients {
}

.clients-container {
  padding: 50px 0 140px;
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(6, 1fr);
  }
}

.client {
  margin: 0 auto;
  &:not(:last-child) {
    margin-bottom: 60px;
  }
  @media screen and (min-width: 768px) {
    &:not(:last-child) {
      margin-bottom: unset;
    }
  }
}

.subscribe-container {
  height: 77px;
  background-color: #252b42;
  &__block {
    padding: 30px 10px;
    min-height: 124px;
    max-width: 986px;
    background-color: #16697a;
    margin: 0 auto;
    transform: translateY(-50%);
    text-align: center;
  }
  &__text {
    font-size: 1.5em; //24px
    line-height: 1.333333333333333em; //32px
    font-weight: 700;
    margin-bottom: 30px;
    display: block;
    color: #fff;
  }
  &__form {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__mail {
    min-height: 58px;
    min-width: 120px;
    padding-left: 20px;
    border: 0;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  &__button {
    min-height: 58px;
    min-width: 117px;
    color: #fff;
    background-color: $orange;
    border: 0;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    outline: none;
    cursor: pointer;
  }
  @media screen and (min-width: 768px) {
    &__block {
      padding: 30px 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__text {
      margin-bottom: unset;
      margin-right: 20px;
      max-width: 262px;
      text-align: left;
    }
    &__mail {
      min-width: 450px;
    }
  }
}
