.about {
  background-color: #fff;
  padding: 100px 0;
  &__title {
    margin-bottom: 10px;
    max-width: 308px;
  }
  &__subtitle {
    margin-bottom: 100px;
    max-width: 225px;
  }
  &__video-and-text {
    display: flex;
    flex-direction: column;
  }
  &__ico {
    font-size: 3em;
    color: $orange;
    margin-right: 20px;
  }
  &__video-block {
    margin-bottom: 70px;
    &__video-container {
      margin: 0 auto;
      max-width: 597px;
      height: 442px;
      background-color: grey;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 2em;
    }
  }
  &__info-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title {
      margin-bottom: 10px;
      max-width: 324px;
      text-align: left;
    }
    &__subtitle {
      margin-bottom: 50px;
      max-width: 330px;
      font-weight: 500;
      margin: 0 auto;
      margin-bottom: 50px;
      color: $p-color;
    }
    &__row {
      display: flex;
      align-items: flex-start;
      &:not(:last-child) {
        margin-bottom: 30px;
      }
      &__text-container {
        text-align: left;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      &__title {
        max-width: 277px;
        margin-bottom: 5px;
        letter-spacing: 0.1px;
        line-height: 1.5em; //24px
        font-weight: 700;
      }
      &__subtitle {
        max-width: 222px;
        font-size: 0.875em; //14px
        line-height: 1.714285714285714em; //24px
        color: $p-color;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    &__title {
      max-width: 470px;
    }
    &__subtitle {
      max-width: 511px;
    }
    &__video-and-text {
      flex-direction: unset;
      justify-content: center;
    }
    &__video-block {
      margin-bottom: unset;
      margin-right: 120px;
      &__video-container {
        width: 400px;
        @media screen and (min-width: 1440px) {
          width: 600px;
        }
      }
    }
  }
}

.video {
  width: 100%;
  height: 100%;
}
