.features {
  background-color: #f3f3f3;
  padding: 100px 0;
  &__title {
    max-width: 250px;
    margin: 0 auto;
    @media screen and (min-width: 1024px) {
      max-width: unset;
    }
  }
  &__subtitle {
    max-width: 230px;
    margin: 0 auto;
    margin-bottom: 75px;
    @media screen and (min-width: 1024px) {
      max-width: 520px;
    }
  }
}

.features-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__card {
    width: 230px;
    min-height: 230px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    @media screen and (min-width: 1024px) {
      &:not(:last-child) {
        margin-bottom: unset;
        margin-right: 30px;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    flex-direction: row;
    justify-content: space-around;
  }
}
