.practice {
  padding: 100px 0;
  &__title {
    max-width: 200px;
  }
  &__subtitle {
    max-width: 225px;
    margin-bottom: 75px;
  }
  @media screen and (min-width: 1024px) {
    &__title {
      max-width: unset;
    }
    &__subtitle {
      max-width: 470px;
    }
  }
}

.practice-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__card {
    width: 230px;
    padding-top: 30px;
    background-color: #fff;
    text-align: left;
    transition: all 0.2s;
    position: relative;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    &__title-text {
      padding: 0 20px 0 30px;
      &__title {
        margin-bottom: 15px;
        max-width: 130px;
        letter-spacing: 0.1px;
        line-height: 1.5em; //24px
      }
      &__text {
        line-height: 1.5em; //26px
        margin: 0 0 30px 0;
        text-align: left;
      }
    }
    &__img-container {
      width: 100%;
      height: 138px;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      &__img {
        width: 120%;
        height: 120%;
        transition: all 0.2s;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    flex-direction: unset;
    align-items: flex-start;
    &__card {
      &:not(:last-child) {
        margin-bottom: unset;
        margin-right: 20px;
      }
    }
  }
}

.practice-cards__card:hover
  > .practice-cards__card__img-container
  > .practice-cards__card__img-container__img {
  width: 100%;
  height: 100%;
  transition: all 0.2s;
}
