.footer-nav {
  background-color: #16697a;
  padding: 50px 0;
  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__block {
    display: flex;
    flex-direction: column;
    color: #fff;
    &:not(:last-child) {
      margin-bottom: 30px;
      margin-right: 30px;
    }
  }
  &__title {
    font-size: 1em;
    line-height: 1.5em; //24px
    font-weight: 700;
    margin-bottom: 20px;
  }
  &__link {
    color: #fff;
    font-weight: 600;
    font-size: 0.875em; //14px
    line-height: 1.714285714285714em; //24px
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    &:hover {
      color: $orange;
    }
  }
  &__ico {
    font-size: 2em;
    color: #8ec2f2;
    font-weight: 700;
  }
  @media screen and(min-width: 768px) {
    &__container {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
    }
    &__block {
      &:not(:last-child) {
        margin-bottom: unset;
        margin-right: unset;
      }
    }
  }
}

.footer-copy {
  background-color: #fff;
  padding: 25px 0;
  &__container {
    display: flex;
    flex-direction: column;
  }
  &__text {
    font-size: 0.875em; //14px
    line-height: 1.714285714285714em; //24px
    color: $p-color;
    font-weight: 600;
    margin-bottom: 50px;
  }
  &__social {
    font-size: 1.5em;
  }
  &__ico {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__facebook {
    color: #335bf5;
  }
  &__instagram {
    color: #e51f5a;
  }
  &__twitter {
    color: #21a6df;
  }
  &__youtube {
    color: #e42f08;
  }
  @media screen and (min-width: 768px) {
    &__container {
      flex-direction: unset;
      justify-content: space-between;
    }
    &__text {
      margin-bottom: unset;
    }
  }
}
