.header {
  background: url("../img/bg.png") center no-repeat;
  background-size: cover;
  height: auto;
  max-width: 1440px;
  margin: 0 auto;
  background-color: #f3f3f3;
  padding-bottom: 200px;
  @media screen and (min-width: 1024px) {
    background-position: top;
    padding-bottom: unset;
  }
}

.header-top {
  padding-top: 36px;
  padding-bottom: 36px;
  display: grid;
  grid-template-columns: 50% 1fr 1fr 1fr;
  align-items: center;
  justify-items: end;
  position: relative;
  background-color: #fff;
  &__logo {
    font-size: 1.5em; //24px
    line-height: 1.333333333333333em; //32em
    justify-self: start;
    &:hover {
      color: $sea-color;
    }
  }
  &__search {
    color: $text-color;
    font-size: 2em;
    @media screen and (min-width: 1024px) {
      grid-column-start: 3;
    }
  }
  &__cart {
    color: $text-color;
    font-size: 2em;
    @media screen and (min-width: 1024px) {
      grid-column-start: 4;
    }
  }
  @media screen and (min-width: 1024px) {
    background-color: unset;
    grid-template-columns: 20% 1fr 5% 5%;
  }
}

.nav {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: 0;
  transform: translateY(100%);
  display: none;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #fff;
  padding: 85px 0;
  &__link {
    font-size: 1.875em; //30px
    line-height: 1.5em; //45px
    color: $p-color;
    padding: 15px;
    &:hover {
      color: $sea-color;
    }
  }
  @media screen and (min-width: 1024px) {
    position: unset;
    transform: unset;
    flex-direction: unset;
    grid-column-start: 2;
    grid-row-start: 1;
    display: flex;
    background-color: unset;
    padding: unset;
    &__link {
      padding: unset;
      font-size: 1.142857142857143em; //14px
      line-height: 1.714285714285714em; //24px
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }
}

.burger-toggle:checked ~ .nav {
  display: flex;
}

.burger-toggle {
  display: none;
}

.burger {
  width: 23px;
  height: 14px;
  position: relative;
  overflow: hidden;
  &__line {
    background-color: $p-color;
    height: 2px;
    display: block;
    position: absolute;
    right: 0;
    transition: all 0.2s linear;
    &-top {
      width: 23px;
      top: 0;
    }
    &-mid {
      width: 17px;
      top: 50%;
      transform: translateY(-50%);
    }
    &-bot {
      width: 12px;
      bottom: 0;
    }
    &-cross-right {
      width: 23px;
      bottom: 0;
      right: -23px;
      transform: translateY(20px) rotate(45deg);
    }
    &-cross-left {
      width: 23px;
      bottom: 0;
      left: -15px;
      transform: translateY(15px) rotate(-45deg);
    }
  }
  @media screen and (min-width: 1024px) {
    display: none;
  }
}

.burger-toggle:checked + label > .burger > .burger__line-top {
  transform: translateX(100%);
}
.burger-toggle:checked + label > .burger > .burger__line-mid {
  transform: translateX(100%);
  transition-delay: 0.1s;
}
.burger-toggle:checked + label > .burger > .burger__line-bot {
  transform: translateX(100%);
  transition-delay: 0.2s;
}
.burger-toggle:checked + label > .burger > .burger__line-cross-right {
  transform: translate(-25px, -6px) rotate(45deg);
  transition-delay: 0.3s;
}
.burger-toggle:checked + label > .burger > .burger__line-cross-left {
  transform: translate(13px, -6px) rotate(-45deg);
  transition-delay: 0.4s;
}

.header-bot {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  &__title {
    max-width: 345px;
    margin-bottom: 40px;
    text-align: center;
    font-size: 2.5em; //40px
    line-height: 1.425em; //57px
    @media screen and (min-width: 1024px) {
      text-align: left;
      font-size: 3.625em; //58px
      line-height: 1.379310344827586em; //80px
      max-width: 520px;
    }
  }
  &__subtitle {
    max-width: 270px;
    font-size: 1.25em; //20px
    line-height: 1.5em; //30px
    margin-bottom: 50px;
    letter-spacing: 0.2px;
    font-weight: 500;
    text-align: center;
    @media screen and (min-width: 1024px) {
      text-align: left;
      max-width: 400px;
      margin-bottom: 35px;
    }
  }
  &__btn-get {
    min-width: 185px;
    min-height: 50px;
    padding: 10px 35px;
    background: #ffa62b;
    border-radius: 37px;
    color: #fff;
    border: 0;
    margin-bottom: 25px;
    font-size: 1.142857142857143em; //14px
    line-height: 2em; //28px
    outline: 0;
    border: 3px solid transparent;
    cursor: pointer;
    transition: all 0.1s linear;
    &:active {
      border: 3px solid $sea-color;
      color: $sea-color;
      transition: all 0.1s linear;
    }
    @media screen and (min-width: 1024px) {
      margin-bottom: unset;
      margin-right: 20px;
    }
  }
  &__btn-learn {
    min-width: 160px;
    min-height: 50px;
    padding: 10px 35px;
    background: transparent;
    color: $sea-color;
    border: 3px solid $sea-color;
    border-radius: 37px;
    margin-bottom: 30px;
    outline: 0;
    transition: all 0.1s linear;
    cursor: pointer;
    &:active {
      border: 3px solid $orange;
      color: $orange;
      transition: all 0.1s linear;
    }
    @media screen and (min-width: 1024px) {
      margin-bottom: unset;
    }
  }
  @media screen and (min-width: 1024px) {
    align-items: flex-start;
  }
}

.header-cards {
  display: flex;
  flex-direction: column;
  align-self: center;
  &__card {
    max-width: 330px;
    padding: 40px;
    background-color: #fff;
    transition: all 0.2s linear;
    &__title {
      font-size: 1.5em; //24px
      line-height: 1.333333333333333em; //32em
      font-weight: 700;
      margin: 0 0 10px 0;
    }
    &__text {
      font-size: 1.142857142857143em; //14px
      line-height: 1.428571428571429em; //20px
      color: $p-color;
    }
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    &:hover {
      background-color: $sea-color;
      transition: all 0.2s linear;
    }
    @media screen and (min-width: 1024px) {
      box-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);
      width: 100%;
      &:not(:last-child) {
        margin-bottom: unset;
        margin-right: 20px;
      }
      &:hover {
        max-width: 340px;
        transition: all 0.2s linear;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    flex-direction: unset;
    transform: translateY(10%);
  }
}
.ico-card_margin {
  margin-bottom: 10px;
}

.header-cards__card:hover > .header-cards__card__title,
.header-cards__card:hover > .header-cards__card__text,
.header-cards__card:hover > .ico-card_hover {
  color: #fff;
  transition: all 0.2s linear;
}

.header-bot_btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 1024px) {
    flex-direction: unset;
    justify-content: center;
    margin-bottom: 80px;
  }
}
